import React, { Component } from 'react';
import { useTranslation, withTranslation, Trans } from 'react-i18next';
import UnityDescription from "./unityDescription/UnityDescription";
import Service from "./service/Service";
import Technology from "./technology/Technology";
import Client from "./client/Client";
import PortfolioCompleted from "./portfolioCompleted/PortfolioCompleted";
import PortfolioOngoing from "./portfolioOngoing/PortfolioOngoing";
import Event from "./event/Event";
import RedesUnity from "./redesUnity/RedesUnity"
import softData from 'data/unit/Soft.json';
import './Soft.css'; 

class Soft extends Component {

  render() {

    const { t, i18n } = this.props;

    return (
      <div className="Soft">
        <UnityDescription unity={softData} />
        <p style={{ color: softData.colorTitles }} className="Soft-title-services">{t('unit.services')}</p>
        <div className="Soft-container-services">
          <img className="Soft-background11-services" src={"https://drive.google.com/uc?id=1wgXgVNFl_S6bU0fcDnbVtXM9B4oofbyP"} alt={"background11"}></img>
          <img className="Soft-background12-services" src={"https://drive.google.com/uc?id=1wgXgVNFl_S6bU0fcDnbVtXM9B4oofbyP"} alt={"background12"}></img>
          <img className="Soft-background21-services" src={"https://drive.google.com/uc?id=1rjxbWkEbTPPTdfXKclXjB3kJU30K94YV"} alt={"background21"}></img>
          <img className="Soft-background22-services" src={"https://drive.google.com/uc?id=1rjxbWkEbTPPTdfXKclXjB3kJU30K94YV"} alt={"background22"}></img>
          {
            softData.services.map(
              (service, index) => {
                return <div key={index} className="Soft-container-service"> 
                <Service 
                service={service} 
                color={softData.color} 
                colorTitle={softData.colorSubTitles} 
                marcoServices={softData.marcoServices} 
                /> </div>
              }
            )
          }
        </div>
        <p style={{ color: softData.colorTitles }} className="Soft-title-services"><Trans i18nKey="unit.techs">1<br />2</Trans></p>
        <div className="Soft-container-technologies">
          {
            softData.technologies.map(
              (technology, index) => {
                return <Technology key={index} technology={technology} color={softData.color} />
              }
            )
          }
        </div>
        <p style={{ color: softData.colorTitles }} className="Soft-title-services">{t('unit.clients')}</p>
        <div className="Soft-container-clients">
          {
            softData.clients.map(
              (client, index) => {
                return <Client key={index} client={client} color={softData.color} />
              }
            )
          }
        </div>
        <p style={{ color: softData.colorTitles }} className="Soft-title-services">{t('unit.portfolio')}</p>
        <div className="Soft-container-portfolioCompleted">
          <div className="Soft-container-background1-portfolioCompleted">
            <img className="Soft-background1-portfolioCompleted" src={"https://drive.google.com/uc?id=1t7KwSZdS2KizI9bVon-RL_lvwV5xPJ39"} alt={"backPortafolio1"}></img>
          </div>
            <img className="Soft-background2-portfolioCompleted" src={"https://drive.google.com/uc?id=124mL9lUu2d-DbJTfvponggcOD12qDHc0"} alt={"backPortafolio2"}></img>
          <p style={{ color: softData.colorSubTitles }} className="Soft-completed-title">{t('unit.completed')}</p>
          {
            softData.portfolio.completed.map(
              (portfolioCompleted, index) => {
                return <PortfolioCompleted 
                  key={index} 
                  index={index} 
                  portfolioCompleted={portfolioCompleted} 
                  color={softData.color} 
                  marcoServices={softData.marcoServices}
                  />
              }
            )
          }
        </div>
        <div className="Soft-container-portfolioOngoing">
          <p style={{ color: softData.colorSubTitles }} className="Soft-completed-title">{t('unit.ongoing')}</p>
          {
            softData.portfolio.ongoing.map(
              (portfolioOngoing, index) => {
                return <PortfolioOngoing 
                key={index} 
                index={index} 
                portfolioOngoing={portfolioOngoing} 
                color={softData.color} 
                marcoSmall={softData.marcoSmallOngoing}
                marcoLarge={softData.marcoLargelOngoing}
                />
              }
            )
          }
        </div>
        <p style={{ color: softData.colorTitles }} className="Soft-title-services">{t('unit.events')}</p>
        <div className="Soft-container-events">
          {
            softData.events.map(
              (event, index) => {
                return <Event 
                key={index} 
                event={event} 
                color={softData.color} 
                marcoServices={softData.marcoServices}
                />
              }
            )
          }
        </div>
        <RedesUnity redes={softData.redes}/>
      </div>
    );
  }
}

export default withTranslation()(Soft);
